* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: darkgrey;
    padding: 40px;
}

.title {
    font-size: 40px;
    margin: 0;
    text-align: center;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
}

main{
    background-color: #f5f5f5;
    height: 500px;
    max-width: 500px;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.die-box{
    display: grid;
    grid-template: auto auto/ repeat(5,1fr);
    gap: 20px;
}

.die{
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.roll {
    background-color: #5035ff;
    height: 50px;
    width: 100px;
    border-radius: 7px;
    color: white;
    align-content: center;
    align-self: center;
    cursor: pointer;
    font-size: larger;
}